<template>
	<form @submit.prevent="submitForm">
		<div>
			<label for="username">username: </label>
			<input type="text" id="username" v-model="user.username" />
		</div>
		<div>
			<label for="password">password: </label>
			<input type="text" id="password" v-model="user.password" />
		</div>
		<div>
			<label for="nickname">nickname: </label>
			<input type="text" id="nickname" v-model="user.nickname" />
		</div>
		<button
			:disabled="!isUserNameValid || !user.password || !user.nickname"
			type="submit"
		>
			SignUpüé®
		</button>
		<p>{{ logMessage }}</p>
	</form>
</template>

<script>
import { signupUser } from '@/api/index';
import { validateEmail } from '@/utils/validation';

export default {
	//data ÏûêÎèôÏôÑÏÑ±Í∏∞Îä• : vda
	data() {
		return {
			user: {
				username: '',
				password: '',
				nickname: '',
			},
			logMessage: '',
		};
	},
	computed: {
		isUserNameValid() {
			console.log(validateEmail(this.user.name));
			return validateEmail(this.user.username);
		},
	},
	methods: {
		//@ÏùÄ Ïù¥Î≤§Ìä∏ v-onÏùò ÏïΩÏûê
		// .preventÎ°ú Ï†úÏ∂úÌõÑ Ïù¥Î≤§Ìä∏ Ïù¥ÎèôÏùÑ Î∞©ÏßÄÌïúÎã§.
		async submitForm() {
			//then, catchÎ≥¥Îã§ async await Ï∂îÏ≤ú
			//destructuringÏùÑ ÌÜµÌï¥ response.dataÎ•º Î∞îÎ°ú Í∫ºÎÇºÏàòÏûàÏùå
			try {
				const { data } = await signupUser(this.user);
				this.logMessage = `${data.nickname}ÎãòÏù¥ Í∞ÄÏûÖÎêòÏóàÏäµÎãàÎã§.‚úî`;
			} catch ({ response }) {
				this.logMessage = response.data;
			} finally {
				this.initForm();
			}
		},
		initForm() {
			this.user = {
				username: '',
				password: '',
				nickname: '',
			};
		},
	},
};
</script>

<style scoped>
.form {
	width: 460px;
	height: 100%;
}
.form .validation-text {
	margin-top: -0.5rem;
	margin-bottom: 0.5rem;
	font-size: 1rem;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
}
.btn {
	color: white;
}
.log {
	width: 460px;
}
</style>
